@colordf: #EE0033;

.db-flex {
	display: flex;
	display: -webkit-flex;
}

.hhv-row-field {
	margin: 0 -15px;
}
.hhv-col50 {
	width: 50%;
	float: left;
	padding: 0 15px;
}
.hhv-col25 {
	width: 25%;
	float: left;
	padding: 0 15px;
}
.hhv-col20 {
	width: 20%;
	float: left;
	padding: 0 15px;
}

.hhv-col33 {
	width: 33.33%;
	float: left;
	padding: 0 15px;
}

.field {
	&.full {
		label {
			display: block;
			width: 100%;
			float: none;
			margin-bottom: 5px;
		}
		.control {
			width: 100%;
			float: none;
			padding: 0;
		}
	}
}

#service-register {
	a.btn {
		min-width: 230px;
	}
}

.hhv-mobile-service {
	.title-box {
		position: relative;
		z-index: 10;
		h2.space1 {
			margin-bottom: 0;
		}
	}
	.inner-item {
		h2 {
			padding: 15px 10px;
			span {
				display: block;
				font-weight: 300;
				font-size: 22px;
				line-height: 24px;
				text-transform: capitalize;
				margin-top: 10px;
			}
		}
		.int {
			font-size: 22px;
			font-weight: 500;
			.sales {
				color: #FE9A00;
				font-size: 14px;
				line-height: 24px;
				font-weight: normal;
				background: rgba(255, 213, 0, 0.15);
				border-radius: 6px;
				padding: 5px 7px;
			    vertical-align: bottom;
			    margin-right: 6px;
			}
			.price-old {
				font-size: 14px;
				line-height: 24px;
				color: #BFC4CE;
				text-decoration-line: line-through;
				font-weight: normal;
			}
		}

	}
}

.apply {
	&.hhv-apply-dvgtgt {
		.sub-tabs {
			.subleft {
				min-height: initial;
				.gtgt {
					.intro-more {
						padding: 20px 30px 30px;
						h2 {
							margin-bottom: 10px;
						}
						.introtv .coll .coll-inner {
							font-size: 14px;
							line-height: 16px;
							.item-desc {
								margin-bottom: 20px;
							}
							.item-price {
								font-size: 12px;
								color: #576C8A;
								.price {
									font-weight: bold;
									font-size: 24px;
									color: @colordf;
								}
							}
						}
					}
				}
				&:hover {
				    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.4);
				    .imgs img {
				    	opacity: 0.5;
				    }
				}
			}
			.subright  {
				.item {
					margin-bottom: 15px;
					padding-bottom: 15px;
					border-bottom: 1px solid #E5E5E5;
					.imgs {
						margin-right: 20px;
						line-height: 0;
						border-radius: 10px;
						overflow: hidden;
					}
					.text {
						.db-flex();
						.box-text {
							flex: 1;
							padding-right: 10px;
							h3 {
								margin-bottom: 15px;
								a {
									color: #252525;
									font-size: 20px;
								}
							} 
							.desc {
								margin-bottom: 15px;
							}
							.price {
								font-weight: normal;
								font-size: 12px;
								color: #576C8A;
								strong {
									font-weight: bold;
									font-size: 24px;
									color: @colordf;
									padding-right: 5px;
								}
							}

						}
						.box-dk {
							width: auto;
							.db-flex();
   							flex-direction: column-reverse;
   							-webkit-flex-direction: column-reverse;
						}
					}
					&:last-child {
						border-bottom: 0;
					}
				}
			}
		}
	}
}

.hhv-info-adress {
	.title-box h2.space1 {
	 	margin-bottom: 20px;
	}
}

.hhv-submemnu {
	margin-bottom: 50px;
	.list-menu {
		text-align: center;
		li {
			display: inline-block;
			vertical-align: top;
			padding: 20px 30px;
			a {
				font-weight: normal;
				font-size: 20px;
				line-height: 23px;
				color: #576C8A;
				padding-bottom: 10px;
				border-bottom: 2px solid transparent;
			}
			&.active, &:hover {
				a {
					color: #EE0033;
					border-color: @colordf;
				}
			}
		}
	}
}

.hhv-list-internet {
	.lists.slider .item {
		box-shadow: none;
		.list-internet {
			.child-item {
				border-radius: 10px;
			    box-shadow: 0px 8px 15px rgba(39, 52, 125, 0.12);
			    margin-bottom: 30px;
				.inner-itm {
					padding: 15px;
					.item-img {
						width: 104px;
						float: left;
						margin-right: 20px;
					}
					.item-content {
						overflow: hidden;
						.db-flex();
						.box-info {
							flex: 1;
							h2 {
								a {
									font-weight: bold;
									font-size: 24px;
									color: #0C0033;
									padding-right: 10px;
								}
								span {
									font-size: 16px;
									color: #0C0033;
									font-weight: normal;
								    vertical-align: middle;
								}
							}
							.rating-summary  {
								margin-bottom: 10px;
							}
							.pr {
								color: #EE0033;
								font-weight: 500;
								font-size: 22px;
								.sales {
									color: #FE9A00;
								    font-size: 14px;
								    line-height: 24px;
								    font-weight: normal;
								    background: rgba(255, 213, 0, 0.2);
								    border-radius: 6px;
								    padding: 5px 7px;
								    vertical-align: bottom;
								    margin-right: 6px;
								}
								.pr-old {
									padding-left: 10px;
									font-size: 14px;
									color: #BFC4CE;
									font-weight: normal;
									text-decoration-line: line-through;
								}
							}
						}
						.box-btn {
							.db-flex();
						    flex-direction: row;
						    -webkit-flex-direction: row;
							align-items: center;
							-webkit-align-items: center;
						}
					}
				}
				&:hover {
					box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.4);
				}
			}
		}
	}
}

.hhv-data-internet {
	.goi-cuoc {
		.minmax {
			.min-maxd {
				h2 {
					color: #0C0033;
					margin-bottom: 20px;
				}
			}
			.ll-gg {
				margin-top: 30px;
			    box-shadow: 4px 4px 16px rgba(0, 0, 0, 0.08);
			    border-radius: 6px;
			    overflow: hidden;
			    .db-flex();
			    .ll {
			    	border-right: 1px solid #ddd;
			    }
				.ll, .gg {
					width: 50%;
					border-radius: 0;
					box-shadow: none;
				}
				.title {
					border-radius: 0;
				}
				.pc-old {
					.price-old {
						color: #576C8A;
						font-size: 14px;
						text-decoration-line: line-through;
						font-weight: normal;
					}
					.sales {
						color: #FE9A00;
						font-size: 14px;
						line-height: 24px;
						font-weight: normal;
						background: rgba(255, 213, 0, 0.15);
						border-radius: 6px;
						padding: 5px 7px;
					    vertical-align: text-bottom;
					    margin-left: 6px;
					    text-decoration-line: none;
					}
				}
			}
		}
	}
}

.box-element-detail {
	margin-bottom: 40px;
}

ul.list-info {
	line-height: 28px;
	font-size: 16px;
}

.hhv-phuong-an {
	.content.tabs {
		padding: 0;
		.btabs {
			.tab-title {
				li {
					margin-right: 100px;
					padding-bottom: 40px;
					padding-left: 5px;
					position: relative;
					
					a {
						padding: 0;
						border: 0;
					}
					.radio-custom1 {
						padding-left: 28px;
						.checkmark {
							top: -1px;
						}
					}
					&.active {
						&:before {
							position: absolute;
							content: "";
							bottom: 0;
							left: 0;
						    border-width: 14px;
						    border-top-color: transparent;
						    border-left-color: transparent;
						    border-right-color: transparent;
						    border-bottom-color: #f7f7f7;
						    border-style: solid;
						}
					}
					&.active, &:hover {
						a {
						    color: #576C8A;
						    font-weight: normal;
						}
						.radio-custom1 {
   							.checkmark {
   								border-color: #EE0033;
   								&:after {
									display: block;
									background: #EE0033;
									width: 10px;
									height: 10px;
									border-radius: 100%;
									position: absolute;
									top: 3px;
									left: 3px;
   								}
   							}
						}
					}
				}
			}
		}
		.tabs-content {
			position: relative;
			background-color: #f7f7f7;
			&:before {
				left: 100%;
				top: 0;
				bottom: 0;
				width: 1000px;
				background-color: #f7f7f7;
				content: "";
				position: absolute;
			}
			&:after {
				right: 100%;
				top: 0;
				bottom: 0;
				width: 1000px;
				background-color: #f7f7f7;
				content: "";
				position: absolute; 
			}
			.sub-tabs {
				.tabcontent {
					padding: 0;
					.inner-tabs {
						padding: 40px 0;
						.inner-item {
						    .radio-cs {
						    	input {
									position: absolute;
									opacity: 0;
									cursor: pointer;
									height: 0;
									width: 0;
						    	}
						    	.checkmark1 {
									box-shadow: 0px 8px 15px rgba(39, 52, 125, 0.12);
								    border-radius: 10px;
								    text-align: center;
								    padding: 60px 15px;
								    background-color: #fff;
								    color: #EE0033;
								    font-size: 20px;
							    	h4 {
							    		color: #0C0033;
							    		font-weight: bold;
										font-size: 24px;
										margin-bottom: 15px;
							    	}
							    	&:hover {
							    		background: rgba(20, 175, 180, 0.15);
						    			box-shadow: none;
						    			cursor: pointer;
							    	}
						    	}
						    	input:checked ~ .checkmark1 {
						    		background: rgba(20, 175, 180, 0.15);
						    		box-shadow: none;
						    	}
						    }
						}
					}
				}
			}
		}
	}
}

.hhv-pay-total {
	position: relative;
	background-color: rgba(20, 175, 180, 0.15);
	padding: 30px 0;
	&:before {
		left: 100%;
		top: 0;
		bottom: 0;
		width: 1000px;
		background-color: rgba(20, 175, 180, 0.15);
		content: "";
		position: absolute;
	}
	&:after {
		right: 100%;
		top: 0;
		bottom: 0;
		width: 1000px;
		background-color: rgba(20, 175, 180, 0.15);
		content: "";
		position: absolute; 
	}
	.el-total {
		font-size: 32px;
		color: #0C0033;
		margin-bottom: 20px;
		padding-bottom: 20px;
		border-bottom: 1px solid #576C8A;
		font-weight: bold;
	}
	.el-phi {
		font-size: 20px;
		font-weight: normal;
		color: #576C8A;
		padding: 10px 0;
	}
}

.hhv-form-hopdong {
	.hhv-hopdong {
		margin: 0 -15px 30px;
		.items {
			width: 50%;
			float: left;
			padding: 0 15px;
			.inner-item {
				box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.15);
				border-radius: 10px;
				.radio-custom1 {
					padding: 30px 70px;
					.db-flex();
					justify-content: space-around;
					-webkit-justify-content: space-around;
					align-items: center;
					-webkit-align-items: center;
					.img-pay {
						margin-bottom: 0;
					}
					.info {
						flex: 1;
					    position: relative;
    					padding-left: 30px;
    					margin-left: 30px;
    					span  {
    						font-weight: normal;
    					}
    					.txt-sim {
    						font-weight: 500;
							font-size: 18px;
							color: #EE0033;
    					}
    					.note {
    						font-size: 14px;
    						font-style: italic;
    						color: #576C8A;
    						margin-top: 5px;
    					}
					}
				}
			}
			.form-info {
				margin-top: 20px;
				display: none;
				.row-frm {
					padding: 15px 0;
					label {
						font-size: 16px;
						font-weight: 500;
						line-height: 24px;
					}
				}
			}
		}
	}
}

@media (max-width: 767px) {
	.hhv-xs-col100 {
		width: 100%;
	}
	.hhv-mobile-service .title-box h2.space1 {
		font-size: 16px;
	}
	.apply.hhv-apply-dvgtgt .sub-tabs .subright .item .imgs {
		margin-bottom: 15px;
	}
	.apply.hhv-apply-dvgtgt .sub-tabs .subright .item .text {
		width: 100%;
	}
	.hhv-list-internet {
		a.viewall {
			position: static;
		}
	}
	.hhv-list-internet .lists.slider .item .list-internet .child-item {
		box-shadow: none;
		border: 1px solid #eee;
	}
	.hhv-list-internet .lists.slider .item .list-internet .child-item:hover {
		box-shadow: none;
	}
	.hhv-list-internet .lists.slider .item .list-internet .child-item .inner-itm {
		text-align: center;
	}
	.hhv-list-internet .lists.slider .item .list-internet .child-item .inner-itm .item-img {
		float: none;
		width: auto;
		margin: 0 0 10px;
	}
	.hhv-list-internet .lists.slider .item .list-internet .child-item .inner-itm .item-content {
		display: block;
	}
	.hhv-list-internet .lists.slider .item .list-internet .child-item .inner-itm .item-content .box-info {
		margin-bottom: 15px;
	}
	.hhv-list-internet .lists.slider .item .list-internet .child-item .inner-itm .item-content .box-info .pr .pr-old {
		display: block;
		margin-top: 10px;
	}
	.hhv-list-internet .lists.slider .item .list-internet .child-item .inner-itm .item-content .box-btn {
		display: block;
	}
} 