/* POPUP */
.modal{
	&.pin-puk__popup{
		max-width: 613px;
		.white-popup-block{
			padding: 15px 8px;
		}
		h1.title-popup{
			font-family: Montserrat;
			font-style: normal;
			font-weight: bold;
			font-size: 24px;
			line-height: 36px;
			text-align: center;
			color: #0C0033;
			margin: 11px 0 37px;
		}
		.field{
			margin: 0 0 20px;
			label{
				font-family: Roboto;
				font-style: normal;
				font-weight: 500;
				font-size: 16px;
				line-height: 24px;
				color: #0C0033;
				float: none;
			}
			.wrap-box{
				display: flex;
				margin-bottom: 33px;
				input{
					width: 50%;
				}
			}
			.control{
				padding: 0;
				.box-capcha{
					border: 1px solid #C4C4C4;
				    border-radius: 10px;
				    width: 193px;
				    position: relative;
				    padding-right: 36px;
				    height: 50px;
				    width: 50%;
				    margin-right: 11px;
				    .img{
				    	display: inline-block;
				    	img{
							height: 37px;
							max-height: none;
							margin: 6px 5px;
				    	}
				    }
				    .refesh{
						position: absolute;
						right: 9px;
						top: 12px;
						width: 25px;
						height: 25px;
						display: block;
						cursor: pointer;
				    }
				}
				input{
					font-size: 14px;
					color: #BFC4CE;
					padding: 14px 16px;
				}
			}
		}
		.action{
			a{
				width: 100%;
				padding: 15px 30px;
				margin-bottom: 10px;
			}
		}
		.modal-close{
			top: 12px;
		}
		.close-modal{
			display: none;
		}
	}
	&.cancel-service__popup{
		max-width: 600px;
		h1.title-popup{
			text-align: center;
		}
		.action{
		    display: flex;
			justify-content: center;
			a.btn{
				font-family: Montserrat;
				font-style: normal;
				font-weight: normal;
				font-size: 14px;
				line-height: 20px;
				text-align: center;
				color: #EE0033;
				background: transparent;
				width: 36%;
				&.action-back{
					margin-right: 40px;
				}
				&:hover{
					background: #14afb4;
					border-color: #14afb4;
					color: #fff;
				}
			}
		}
		.close-modal{
			display: none;
		}
	}
}

