/* responsive */
@media (max-width: 1199px){
	.main-pay .content.tabs{
		.tabcontent .col-payl .row-group .control .currency ul li{
			float: none;
			display: inline-block;
			&:nth-child(3n+3){
				margin-right: 16px;
				margin-bottom: 16px;
			}
		}
