.clearfix {
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}

.txt-black {
  color: #0c0033;
}

.txt-blue-light {
  color: #14d6cf;
}

.form-note {
  display: block;
  font-style: italic;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  color: #576c8a;
  font-family: "Roboto";
  text-transform: none;
}

//*********************************
// NHAP THONG TN CA NHAN
//*********************************

.c-personal-info {
  margin-bottom: 70px;

  @media (min-width: 768px) {
    padding-top: 20px;

    .tit {
      margin-bottom: 60px;
    }
  }

  .tab-title {
    .clearfix;
    text-align: center;

    li {
      float: none;
      display: inline-block;

      @media (min-width: 992px) {
        & + li {
          margin-left: 83px;
        }
      }
    }
  }
}

.frm-cart--personal-info {
  form {
    box-shadow: 0px 10px 40px rgba(39, 52, 125, 0.12);
    border-radius: 10px;
    padding: 40px 30px;
    margin-bottom: 40px;
  }
}

.form-group {
  &--action {
    .btn {
      font-style: normal;
      font-weight: normal;
      width: 165px;
      max-width: 100%;
      border-width: 1px;

      @media (max-width: 479px) {
        width: auto;
      }

      &:not(.btn-blue) {
        font-family: "Roboto";
      }

      & + .btn {
        margin-left: 30px;
      }
    }
  }
}

// UPLOAD HO SO
.btn-upload {
  width: 180px;
  height: 180px;
  background: #e7e7e7;
  border-radius: 10px;
  position: relative;
  margin: 0 auto;

  label {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    i {
      font-size: 32px;
      color: @color-1;
    }
  }
}

.file-upload {
  display: none;
}

.frm-upload {
  form {
    width: 89%;
    margin: 0 auto 64px;
    text-align: center;
  }

  .txt-blue-light {
    margin-bottom: 60px;
  }

  .txt-black {
    margin-bottom: 40px;
  }

  .upload-note {
    color: #14d6cf;
    margin-bottom: 60px;
    display: block;
  }

  .agree {
    font-style: normal;
  }

  .checkbox {
    display: inline-block;
    margin-bottom: 30px;
  }
}

.form-group--upload {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  &::after {
    display: none;
  }

  .label {
    color: #576c8a;
    font-size: 16px;
    display: block;
    margin-bottom: 10px;
    text-align: center;
  }

  @media (max-width: 767px) {
    .form-group-item {
      width: 100%;
      text-align: center;

      & + .form-group-item {
        margin-top: 30px;
      }
    }
  }
}

// *********************
// HOP DONG
// *********************

.ll-gg--v2 {
  border-radius: 10px;

  > .title {
    font-size: 24px;
    padding: 25px 30px;
    text-align: left;
  }

  .body {
    padding: 32px 30px;
  }

  & + .ll-gg--v2 {
    margin-top: 60px;
  }

  .field {
    label.pt-0 {
      padding-top: 0;
    }

    & + .field {
      margin-top: 15px;
    }

    .control {
      min-height: 50px;
      padding-left: calc((100% - 180px * 3) / 2 + 180px);

      strong {
        font-size: 18px;
        color: #0c0033;
      }

      &--w100 {
        width: 100%;
        float: left;

        .ll-gg {
          display: flex;
        }
      }
    }
  }

  @media (max-width: 767px) {
    .field {
      label {
        padding-top: 0;
        margin-bottom: 10px;
      }

      & + .field {
        margin-top: 0;
      }

      .control {
        padding-left: 0;
        width: 100%;
      }

      &--method {
        margin-top: 20px !important;
        margin-bottom: 20px;
      }
    }
  }
}

.o-goicuoc {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  border-radius: 10px 0 0 10px;

  &__name {
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-right: 1px solid #ededed;
    padding: 16px;

    h2 {
      color: #0c0033;
      font-size: 36px;
      font-family: "Montserrat";
    }
  }

  &__speed {
    width: 30%;
    padding-bottom: 20px;
    border-right: 1px solid #ededed;

    .title {
      border-radius: 0;
    }
  }

  &__price {
    width: 40%;
    padding-bottom: 20px;

    .title {
      border-top-left-radius: 0;
    }
  }

  @media (max-width: 991px) and (min-width: 768px) {
    &__name {
      h2 {
        font-size: 30px;
      }
    }
  }

  @media (max-width: 767px) {
    > div {
      width: 100% !important;
    }
  }
}

.field--method {
  label {
    font-size: 18px;
    display: block;
    width: 100%;
    padding-bottom: 8px;
    border-bottom: 1px solid #576c8a;
  }

  select {
    border: none;
    padding-left: 0;
    padding-right: 45px;
    font-size: 20px;
    color: #576c8a;
    line-height: 1.8;
    font-weight: 400;
    padding-top: 4px;
    padding-bottom: 4px;
    width: auto;
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='16' height='9' viewBox='0 0 16 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.863231 0.863231C1.12358 0.602881 1.54569 0.602881 1.80604 0.863231L8.0013 7.05849L14.1966 0.863231C14.4569 0.602881 14.879 0.602881 15.1394 0.863231C15.3997 1.12358 15.3997 1.54569 15.1394 1.80604L8.47271 8.47271C8.21236 8.73306 7.79025 8.73306 7.5299 8.47271L0.863231 1.80604C0.602881 1.54569 0.602881 1.12358 0.863231 0.863231Z' fill='%23576C8A'/%3e%3c/svg%3e ");
  }
}

.hhv-pay-total--v2 {
  background: rgba(255, 213, 0, 0.15);
  padding: 30px;
  width: calc(100% + 60px);
  margin: 0 -30px -32px;

  .el-total {
    margin: 0 -30px 20px;
    padding: 0 30px 20px;
  }

  &::before,
  &::after {
    display: none;
  }
}

.row-group--method {
  label {
    padding-top: 0;
  }

  .radio-custom1 {
    width: 100%;
    padding: 2px 0 0 32px;

    font-weight: 400;

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    input:checked ~ .checkmark ~ span {
      font-weight: 400;
    }
  }
}

.customer-info {
  padding: 10px 30px 30px;

  li {
    & + li {
      margin-top: 20px;
    }

    display: flex;

    .label {
      width: calc((100% - 180px * 3) / 2 + 180px);
      max-width: 100%;
      color: #576c8a;

      i {
        display: inline-block;
        width: 20px;
      }
    }

    .value {
      color: #0c0033;
    }
  }

  @media (max-width: 767px) {
    li {
      flex-wrap: wrap;

      .label {
        width: 100%;
        margin-bottom: 10px;
      }
    }
  }
}

body {
  color: blue;
}
