.page-qltkdd {

    //fix bug Trang quản lý tk FTTH/ internet
    &.page-ftth {

        .tabs__detail__content__left__price__sale {
            text-decoration: line-through;
            font-weight: 300;
        }

        .error-service__content-item {
            position: relative;

            &:after {
                content: '';
                background: linear-gradient(0deg, #00C295 0.34%, rgba(0, 194, 149, 0.53901) 41.39%, rgba(0, 194, 149, 0.0001) 99.81%);
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                top: 0;
                z-index: 0;
            }

            a {

                span {
                    position: relative;
                    z-index: 1;
                }
            }
        }
    }

    .login-register {

        ul {

            > li {

                > div {

                    &.hello {

                        span {

                            &.txt {

                                b {
                                    text-align: right;
                                }

                                .phone {
                                    padding-right: 5px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .owl-carousel {

        .owl-stage-outer {
            padding-top: 33px;
        }
    }

    .tabs {

        h3 {
            font-weight: bold;
            font-size: 24px;
            line-height: 28px;
            text-transform: uppercase;
            color: #252525;
        }

        &.tabs-dvdsd {

            .btn {
                padding-left: 22px;
                padding-right: 22px;
                min-width: 100px;
            }
        }
    }

    .tab-title {

        li {
            padding: 0 15px;

            a {
                padding: 0;
            }
        }
    }

    .content {

        .tit {

            .btabs {
                margin: 4px 0 0 44px;
            }
        }
    }

    .tabs__detail__content__left__date {

        i {
            position: relative;
            top: 2px;
        }
    }

    .tabs__detail {
        background: #EE0033;
        border-top: 1px solid rgba(255, 255, 255, .5);
        padding-bottom: 5px;

        &__info {
            text-align: center;
            padding: 18px 0;

            &__name {
                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 20px;
                letter-spacing: 0.1px;
                text-transform: uppercase;
                color: #FFFFFF;

                &:after {
                    content: '';
                    background: transparent url("../images/i-check.svg") no-repeat left top;
                    background-size: contain;
                    width: 17px;
                    height: 11px;
                    display: inline-block;
                    margin-left: 8px;
                }
            }

            &__phone,
            &__manage--order {
                font-weight: 300;
                font-size: 16px;
                line-height: 19px;
                color: #FFFFFF;

                &:before {
                    content: '|';
                    margin-left: 3px;
                    margin-right: 10px;
                }

                &:after {
                    content: '';
                    background: transparent url("../images/i-check.svg") no-repeat left top;
                    background-size: contain;
                    width: 10px;
                    height: 8px;
                    display: inline-block;
                    margin-left: 7px;
                }
            }

            &__bot {
                padding: 17px 0 0;
            }

            &__cart,
            &__wishlist {
                font-weight: 300;
                font-size: 12px;
                line-height: 14px;
                letter-spacing: 0.125px;
                text-transform: capitalize;
                color: #FFFFFF;

                &:before {
                    content: '';
                    width: 17px;
                    height: 16px;
                    display: inline-block;
                    margin-right: 10px;
                    position: relative;
                    top: 3px;
                }
            }

            &__cart {

                &:before {
                    background: transparent url("../images/i-cart.svg") no-repeat left top;
                    background-size: contain;
                }

                &:after {
                    content: '|';
                    margin-left: 10px;
                    margin-right: 9px;
                }
            }

            &__wishlist {

                &:before {
                    background: transparent url("../images/i-wishlist.svg") no-repeat left top;
                    background-size: contain;
                }
            }
        }

        &__content {
            display: flex;
            flex-flow: row wrap;

            &__left,
            &__right {
                width: 30%;
            }

            &__mid {
                width: 40%;
            }

            &__left {
                padding: 5px 0 0;

                &__text {
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 19px;
                    text-align: center;
                    color: #FFFFFF;
                    position: relative;
                    top: -2px;
                    margin-right: 10px;
                }

                &__price {
                    font-family: 'Montserrat';
                    font-weight: 500;
                    font-size: 24px;
                    line-height: 50px;
                    text-transform: uppercase;
                    color: #FFFFFF;

                    &.before--money {

                        &:after {
                            content: '';
                            background: transparent url("../images/i-check.svg") no-repeat left top;
                            background-size: contain;
                            width: 18px;
                            height: 12px;
                            display: inline-block;
                            margin-left: 10px;
                        }
                    }
                }
            }

            &__mid {
                text-align: center;

                &__text {
                    font-family: 'SF Pro Text';
                    background: #FE9A00;
                    border-radius: 2px;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 30px;
                    text-align: center;
                    color: #FFFFFF;
                    display: inline-block;
                    padding: 4px 8px 2px;
                    position: relative;
                    top: -10px;
                    margin-right: 20px;
                }

                &__price {
                    font-family: 'Montserrat';
                    font-weight: 600;
                    font-size: 45px;
                    line-height: 50px;
                    letter-spacing: 0.4px;
                    text-transform: uppercase;
                    color: #FFFFFF;

                    &:after {
                        content: '';
                        background: transparent url("../images/i-arrow.svg") no-repeat left top;
                        background-size: contain;
                        width: 18px;
                        height: 24px;
                        display: inline-block;
                        margin-left: 15px;
                    }
                }

                &__bot {
                    padding: 23px 0 0;

                    .btn {
                        font-family: 'Montserrat';
                        box-shadow: 0 10px 20px rgba(177, 186, 201, 0.15);
                        font-weight: 300;
                        font-size: 14px;
                        line-height: 20px;
                        text-align: center;
                        min-width: 180px;
                        margin: 0 10px;
                    }

                    .btn-tracuoc {
                        color: #FFFFFF;
                        background: rgba(255, 255, 255, 0.2);
                        border: 2px solid #EEF1F7;

                        &:hover,
                        &:focus {
                            background: #fff;
                            border: 2px solid #fff;
                            color: #EE0033;
                        }
                    }

                    .btn-checkout {
                        background: #FFFFFF;
                        border: 2px solid #EEF1F7;
                        color: #EE0033;

                        &:hover,
                        &:focus {
                            background: rgba(255, 255, 255, 0.2);
                            color: #fff;
                        }
                    }
                }
            }

            &__right {
                text-align: right;

                &__text {
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 19px;
                    text-align: center;
                    color: #FFFFFF;
                    padding-right: 10px;
                    position: relative;
                    top: -3px;
                }

                &__point {
                    font-family: 'Montserrat';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 24px;
                    line-height: 50px;
                    text-transform: uppercase;
                    color: #FFFFFF;
                }

                &__bot {
                    padding: 8px 0 0;
                }

                &__member {
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 16px;
                    color: #FFFFFF;
                    opacity: 0.8;
                    display: inline-block;
                    padding-right: 16%;

                    i {
                        margin-right: 10px;
                        position: relative;
                        top: 1px;
                    }
                }
            }
        }
    }

    .tabs-pack-data {
        overflow: hidden;
        position: relative;
        margin-top: -4px;

        &:before {
            content: '';
            background: #EE0033;
            position: absolute;
            border-radius: 100%;
            height: 850px;
            left: -10%;
            right: 0;
            top: -640px;
            width: 120%;
        }

        .content {
            width: 780px;
        }

        .owl-stage-outer {
            margin: 0;
            padding: 40px 0;
            overflow: inherit;
        }

        .item {
            background: #fff;
            margin: 0 25px;
        }
    }

    .pack-data {

        &__tit {
            font-weight: bold;
            font-size: 20px;
            line-height: 23px;
            color: #576C8A;
            text-align: center;
            padding: 30px 0 0;
        }

        &__detail {
            padding: 38px 0 34px;
        }

        &__data,
        &__date {
            float: left;
            padding: 0 50px;
            width: 50%;

            span {
                display: block;
            }
        }

        &__data {

            .pack-data__circle {
                background: transparent url("../images/i-pack-circle-sort1.svg") no-repeat left top;
                background-size: contain;
            }
        }

        &__date {

            .pack-data__circle {
                background: transparent url("../images/i-pack-circle-sort2.svg") no-repeat left top;
                background-size: contain;
            }
        }

        &__circle {
            height: 138px;
            overflow: hidden;
            display: flex;
            flex-flow: column wrap;
            justify-content: flex-end;
            padding: 0 25px 5px;
        }

        &__text__big {
            font-family: 'Montserrat';
            font-weight: bold;
            font-size: 24px;
            line-height: 36px;
            text-align: center;
            color: #0C0033;
            padding-bottom: 2px;
        }

        &__text__small {
            font-size: 16px;
            line-height: 18px;
            display: flex;
            align-items: center;
            text-align: center;
            color: #BFC4CE;
        }

        &__action {
            border-top: 1px solid #EDEDED;
            text-align: center;
            padding: 26px 0 28px;
        }

        &__action__link {
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #EE0033;
            width: calc(~'100% / 3');
            float: left;

            &:before {
                content: '';
                height: 20px;
                display: inline-block;
                margin-right: 5px;
                position: relative;
            }

            &.pack__share {

                &:before {
                    background: transparent url("../images/i-pack-share.svg") no-repeat;
                    top: 7px;
                    width: 16px;
                }
            }

            &.pack__circle {

                &:before {
                    background: transparent url("../images/i-pack-circle.svg") no-repeat;
                    top: 5px;
                    width: 20px;
                }
            }

            span {
                text-decoration: underline;
            }
        }
    }

    .box-data-cacgoi {

        h2 {

            span {
                line-height: 26px;
            }
        }
    }

    .tabs-utilities {
        margin: 0 0 -8px;

        .lists {

            &.slider {

                .owl-stage-outer {
                    padding-top: 28px;
                    margin: 0;
                }
            }
        }

        .owl-nav {

            button {
                top: 46%;
            }
        }

        .tit {
            margin: 0 0 13px;
        }

        .utilities__image {
            float: left;
            padding-left: 48px;
            width: 45%;
        }

        .list-utilities {
            float: right;
            width: 52%;
        }
    }

    .item {

        &__utilities {
            box-shadow: 0 10px 20px rgba(39, 52, 125, 0.12);
            border-radius: 15px;
            display: flex;
            flex-flow: row wrap;
            align-items: center;
            overflow: hidden;
            margin-bottom: 35px;
            height: 90px;

            &.item__box1 {

                .item__utilities__img {
                    background: linear-gradient(314.1deg, #FF780F 1.81%, #FFBA23 97.76%);
                }
            }

            &.item__box2 {

                .item__utilities__img {
                    background: linear-gradient(135.63deg, #29E2E6 0%, #009B94 98.92%);
                }
            }

            &:last-child {
                margin-bottom: 0;
            }

            &:hover {
                box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);

                .intro {

                    &__tit,
                    &__text {
                        color: #EE0033;
                    }
                }
            }

            &__img {
                padding: 22px;
                width: 90px;
            }

            &__intro {
                padding: 13px 10px 5px 13px;
                width: calc(~'100% - 90px');
            }
        }
    }

    .intro {

        &__tit {
            font-weight: bold;
            font-size: 16px;
            line-height: 19px;
            color: #4D4D4D;
        }

        &__text {
            font-weight: normal;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.0875px;
            color: #252525;
            padding-right: 30px;
            padding-top: 8px;
        }
    }

    .tabs-lookups {
        background: linear-gradient(0deg, rgba(20, 175, 180, 0.15), rgba(20, 175, 180, 0.15)), #FFFFFF;
        padding: 58px 0 28px;

        .lists {

            &.slider {

                .owl-carousel {
                    padding: 0 37px;
                }

                .owl-stage-outer {
                    padding-bottom: 55px;
                    margin-bottom: 0;
                }
            }
        }

        .item {
            box-shadow: inherit;
            margin: 0 15px;

            &:hover {
                box-shadow: inherit;

                img {
                    opacity: 1;
                }
            }

            &__title {
                padding-top: 18px;
                padding-bottom: 3px;
            }

            &__text--small {
                font-weight: normal;
                font-size: 14px;
                line-height: 16px;
                letter-spacing: 0.0875px;
                color: #252525;
            }
        }

        .imgs {

            img {
                border-radius: 50%;
                box-shadow: 4px 4px 16px rgba(0, 0, 0, 0.14);
                width: inherit;
            }
        }

        button {

            &.owl-dot {

                span {
                    background: #fff;
                }

                &.active {

                    span {
                        background: #11ACA5;
                    }
                }
            }
        }
    }

    .box-data-cacgoi {

        .inner-item {

            h2 {

                &.h2-minmax {

                    &:after {
                        top: inherit;
                        left: -12%;
                        right: 0;
                        bottom: -7%;
                    }
                }

                &.h2-minmax1 {

                    &:after {
                        top: inherit;
                        left: -25%;
                        right: 0;
                        bottom: 38%;
                    }
                }

                &.h2-minmax2 {
                    background: linear-gradient(339.95deg, #FF780F 1.81%, #FFBA23 97.76%);

                    &:after {
                        top: inherit;
                        right: 7%;
                        bottom: 31%;
                        width: 350px;
                        height: 350px;
                    }
                }

                &.h2-minmax3 {
                    background: linear-gradient(160.52deg, #29E2E6 0%, #009B94 98.92%);

                    &:after {
                        content: '';
                        background: rgba(255, 255, 255, 0.2);
                        position: absolute;
                        left: 20%;
                        border-radius: 100%;
                        width: 660px;
                        height: 660px;
                        bottom: -100%;
                    }
                }

                span {
                    text-transform: none;
                }
            }
        }
    }

    .tabs-dvdsd {

        .lists {

            &.slider {

                .item {
                    margin: 0 15px;
                }
            }
        }

        .btn {
            float: right;
        }
    }

    .item {

        &__box {
            padding: 20px 22px 28px;
        }

        &__title {
            color: #4D4D4D;
            font-weight: bold;
            font-size: 24px;
            line-height: 28px;
            padding-bottom: 7px;
        }

        &__content {
            float: left;
        }

        &__text {
            font-weight: normal;
            font-size: 16px;
            line-height: 19px;
            color: #576C8A;
            padding-top: 1px;
        }

        &__price {
            font-weight: bold;
            font-size: 16px;
            line-height: 19px;
            color: #576C8A;
            padding-top: 7px;

            i {
                font-weight: normal;
                margin-right: 5px;
            }
        }
    }

    .ud-viettel {

        .item {
            box-shadow: inherit;
            margin: 0 15px;

            &:hover {
                box-shadow: inherit;

                .images_content-plus {
                    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);

                    img {
                        opacity: .5 !important;
                    }
                }
            }

            .images_content-plus {
                border-radius: 10px;
            }
        }

        .item__title {
            padding-top: 16px;
            padding-bottom: 0;
        }
    }

    .tabs-dvdsd {

        .tabs-dvdsd__notfound {
            text-align: center;


            .btn-list-pack {
                float: none;
                margin: 20px 0;
                background: #EE0033;
                color: #fff;
                min-width: 192px;

                &:hover,
                &:focus {
                    background: #fff;
                    color: #EE0033;
                }
            }
        }
    }

    .tabs-dvdsd__notfound__text {
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.0875px;
        color: #252525;
    }

    .tabs__detail__content__left__time,
    .tabs__detail__content__mid__time {
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        color: #FFFFFF;
        opacity: 0.85;
        display: block;

        &:before {
            content: '';
            background: transparent url("../images/i-time.svg") no-repeat;
            background-size: contain;
            top: 2px;
            width: 16px;
            height: 16px;
            display: inline-block;
            margin-right: 9px;
            position: relative;
        }
    }

    .tabs__detail__content__mid__time {
        text-align: center;
        padding: 7px 0 0;
    }

    .tabs__detail__content__left__time {
        text-align: left;
        padding: 12px 0 0 16%;
    }
}

@media only screen and (max-width: 1200px) {

    .page-qltkdd {

        .tabs__detail__content__mid__bot {

            .btn {
                min-width: 170px;
            }
        }

        .tabs-utilities {

            .utilities__image {
                width: 40%;
            }

            .list-utilities {
                width: 60%;
            }
        }
    }
}

@media only screen and (max-width: 1023px) {

    .page-qltkdd {

        //fix bug Trang quản lý tk FTTH/ internet
        &.page-ftth,
        &.page-ftth.page-ftth3 {

            .tabs__detail__content__left__price__sale,
            .tabs__detail__content__left__date i {
                margin-left: 0;
            }

            .tabs__detail__content__channel__text {
                display: block;
            }

            .tabs__detail__content__right__top-channel {
                padding-right: 0;

                .tabs__detail__content__channel__point {
                    font-size: 20px;
                }
            }

            .tabs__detail__content__left__price {
                font-size: 20px;
                white-space: nowrap;
            }

            .tabs-utilities {

                .list-utilities,
                .utilities__image {
                    width: 100%;
                }

                .utilities__image {
                    padding: 20px 0;
                    text-align: center;
                }

                .content {

                    .lists.slider {

                        .list-utilities {
                            padding: 20px 50px 10px;

                            .item {
                                margin: 0;
                            }
                        }

                        .item__utilities {
                            width: calc(~"50% - 15px");

                            &:nth-child(2n + 1) {
                                margin-right: 15px;
                            }

                            &:nth-child(2n + 2) {
                                margin-left: 15px;
                            }
                        }
                    }
                }
            }
        }

        &.page-ftth.page-ftth3 {

            .tabs-utilities {

                .content {

                    .lists.slider {
                        flex-flow: column wrap;

                        .item__utilities {

                            &:nth-child(2n + 1),
                            &:nth-child(2n + 2) {
                                margin: 0 auto 20px;
                            }
                        }
                    }
                }
            }
        }

        .tabs__detail__content__left,
        .tabs__detail__content__right {
            width: 23%;
        }

        .tabs__detail__content__mid {
            width: 54%;
        }

        .tabs__detail__content__left__text,
        .tabs__detail__content__left__price {
            display: block;
            margin: 0;
            text-align: left;
        }

        .tabs__detail__content__right__point,
        .tabs__detail__content__left__price {
            font-size: 20px;
        }

        .tabs__detail__content__mid__price {
            font-size: 35px;
        }

        .tabs__detail__content__right__text,
        .tabs__detail__content__right__point {
            display: block;
            margin: 0;
            text-align: right;
        }

        .tabs__detail__content__right__member {
            padding: 0;
        }

        .tabs__detail__content__left__time {
            padding-left: 0;
        }

        .tabs-utilities {
            padding: 0 0 40px;

            .utilities__image,
            .list-utilities {
                width: 100%;
            }

            .utilities__image {
                padding: 20px 0;
                text-align: center;
            }

            .list-utilities {
                padding: 0 50px;
            }
        }

        .mobile-service {

            &.tabs-lookups {
                overflow: unset;
            }
        }

        .tabs {

            h3 {

                &.left {
                    float: none;
                    margin: 0 0 10px;
                }
            }
        }

        .content {

            .tit {

                .btabs {
                    margin-left: 0;
                }
            }
        }
    }
}

@media only screen and (max-width: 767px) {

    .page-qltkdd {

        //fix bug Trang quản lý tk FTTH/ internet
        &.page-ftth.page-ftth3,
        &.page-ftth {

            .tabs__detail__content__channel__text {
                display: inline-block;
            }

            .tabs__detail__content__right__top-channel {
                padding: 10px 0 0;
            }

            .tabs-utilities {
                margin-bottom: 15px;

                .content {

                    .lists.slider {
                        padding: 20px 10px 10px;

                        .list-utilities {
                            padding: 20px 0 20px;
                        }

                        .item__utilities {
                            width: 100%;
                            margin-bottom: 30px;

                            &:nth-child(2n + 1) {
                                margin-right: 0;
                            }

                            &:nth-child(2n + 2) {
                                margin-left: 0;
                            }
                        }
                    }
                }
            }

            .error-service {
                margin-bottom: 15px;

                .error-service__content {

                    .error-service__content-inner {
                        flex-flow: column wrap;

                        .error-service__content-item {
                            width: 100%;
                            margin: 0 0 30px;
                        }
                    }
                }
            }
        }

        .tabs__detail__content__left,
        .tabs__detail__content__right,
        .tabs__detail__content__mid {
            width: 100%;
            text-align: center;
        }

        .tabs__detail__content__right__point,
        .tabs__detail__content__right__text,
        .tabs__detail__content__left__text,
        .tabs__detail__content__left__price {
            display: inline-block;
        }

        .tabs__detail__content__right__text,
        .tabs__detail__content__left__text {
            margin-right: 10px;
        }

        .tabs__detail__info__name {
            display: block;
            padding-bottom: 5px;
        }

        .tabs__detail__content__left__time {
            text-align: center;
            padding: 0 0 10px;
        }

        .tabs__detail__info__phone {

            &:before {
                display: none;
            }
        }

        .tabs__detail__content__right__top {
            padding: 15px 0 0;
        }

        .tabs__detail__content__right__bot {
            padding: 0;
        }

        .tabs__detail__content__mid__price {
            font-size: 30px;

            &:after {
                height: 20px;
            }
        }

        .tabs__detail__content__mid__text {
            top: -5px;
        }

        .tabs__detail__content__mid__bot {

            .btn-checkout {
                margin: 20px 0 0;
            }
        }

        .tabs-pack-data {

            &:before {
                content: '';
                background: #EE0033;
                position: absolute;
                border-radius: 100%;
                height: 400px;
                left: -10%;
                right: 0;
                top: -300px;
                width: 120%;
            }

            .item {
                margin: 5px;
            }
        }

        .pack-data__date,
        .pack-data__data {
            width: 100%;
            float: none;
            padding: 0 10px;
        }

        .pack-data__date {
            margin-top: 30px;
        }

        .pack-data__circle {
            width: 280px;
            margin: 0 auto;
        }

        .pack-data__action__link {
            width: 100%;
            margin-top: 20px;

            &.pack__share {
                margin-top: 0;
            }
        }

        .tabs-lookups .item {
            text-align: center;

            img {
                margin: 0 auto;
            }
        }

        .tabs-utilities {

            .list-utilities {
                padding: 0 10px;
            }
        }

        .intro__text {
            padding-right: 0;
        }
    }
}

@media only screen and (max-width: 639px) {

    .modal {

        &.cancel-service__popup {

            .action {
                flex-flow: column wrap;

                a.btn {
                    width: 100%;

                    &.action-back {
                        margin: 0 0 20px;
                    }
                }
            }
        }
    }
}